@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700;800&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: #f1f6ff;
  font-family: "Poppins", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

::-webkit-scrollbar {
  width: 0px; /* Remove scrollbar space */
  background: transparent; /* Optional: just make scrollbar invisible */
}
.signin__container {
  position: relative;
  width: 100%;
  background-color: #fff;
  min-height: 100vh;
  overflow: hidden;
}

.signin__formContainer {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}

.signin__homeTitle {
  z-index: 100;
  position: relative;
  padding-top: 20px;
  padding-left: 20px;
  font-size: 2rem;
  font-weight: 400;
  color: white;
}
.signin__formDiv {
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
  left: 75%;
  width: 50%;
  transition: 1s 0.7s ease-in-out;
  display: grid;
  grid-template-columns: 1fr;
  z-index: 5;
}

.signin__form {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 0rem 5rem;
  transition: all 0.2s 0.7s;
  overflow: hidden;
  grid-column: 1 / 2;
  grid-row: 1 / 2;
  z-index: 2;
}

.signin__title {
  font-size: 2.2rem;
  font-weight: 500;
  color: #444;
  margin-bottom: 10px;
}

.signin__inputField {
  max-width: 380px;
  width: 100%;
  background-color: #f0f0f0;
  margin: 10px 0;
  height: 55px;
  border-radius: 55px;
  display: grid;
  grid-template-columns: 15% 85%;
  padding: 0 0.4rem;
  position: relative;
}

.signin__inputField .signin__icon {
  color: #acacac;
  margin-top: 25%;
  margin-left: 25%;
}

.signin__inputField input {
  background: none;
  outline: none;
  border: none;
  line-height: 1;
  font-weight: 600;
  font-size: 1.1rem;
  color: #333;
}

.signin__inputField input::-webkit-input-placeholder {
  color: #aaa;
  font-weight: 500;
}

.signin__inputField input:-ms-input-placeholder {
  color: #aaa;
  font-weight: 500;
}

.signin__inputField input::placeholder {
  color: #aaa;
  font-weight: 500;
}
.signin__inputField input:-webkit-autofill {
  box-shadow: 0 0 0px 1000px #f0f0f0 inset;
  border-radius: 20px;
}
.signin__btn {
  width: 150px;
  background-image: linear-gradient(
    to right,
    #42d0ff 0%,
    rgb(68, 148, 253) 51%,
    #43cffd 100%
  );
  background-size: 200% auto;
  border: none;
  outline: none;
  height: 49px;
  border-radius: 49px;
  color: #fff;
  text-transform: uppercase;
  font-weight: 600;
  margin: 10px 0;
  cursor: pointer;
  transition: 0.5s;
}

.signin__btn:hover {
  background-position: right center;
  text-decoration: none;
}
.signin__panelContainer {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}

.signin__container:before {
  content: "";
  position: absolute;
  height: 2000px;
  width: 2000px;
  top: -10%;
  right: 48%;
  transform: translateY(-50%);
  background-image: linear-gradient(-45deg, #4481eb 0%, #04befe 100%);
  transition: 1.8s ease-in-out;
  border-radius: 50%;
  z-index: 6;
}

.signin__panelImage {
  width: 400px;
  height: 300px;
  transition: transform 1.1s ease-in-out;
  transition-delay: 0.4s;
}

.signin__panel {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: space-around;
  text-align: center;
  z-index: 6;
  pointer-events: all;
  padding: 9rem 17% 2rem 12%;
}

.signin__panel .signin__panelContent {
  color: #fff;
  transition: transform 0.9s ease-in-out;
  transition-delay: 0.6s;
}

.signin__panel h3 {
  font-weight: 600;
  line-height: 1;
  font-size: 1.5rem;
}

.signin__panel p {
  font-size: 0.95rem;
  padding: 0.7rem 0;
}

.signin__btn.transparent {
  margin: 0;
  background: none;
  border: 2px solid #fff;
  width: 130px;
  height: 41px;
  font-weight: 600;
  font-size: 0.8rem;
}

@media (max-width: 870px) {
  .signin__container {
    min-height: 800px;
    height: 100vh;
  }
  .signin__formDiv {
    width: 100%;
    top: 95%;
    left: 50%;
    transform: translate(-50%, -100%);
    transition: 1s 0.8s ease-in-out;
  }

  .signin__panelContainer {
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 2fr 1fr;
  }

  .signin__panel {
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    padding: 2.5rem 8%;
    grid-column: 1 / 2;
    grid-row: 1 / 2;
  }

  .signin__panelImage {
    width: 200px;
    transition: transform 0.9s ease-in-out;
    transition-delay: 0.6s;
  }

  .signin__panel .signin__content {
    padding-right: 15%;
    transition: transform 0.9s ease-in-out;
    transition-delay: 0.8s;
  }

  .signin__panel h3 {
    font-size: 1.2rem;
  }

  .signin__panel p {
    font-size: 0.7rem;
    padding: 0.5rem 0;
  }

  .signin__btn.transparent {
    width: 110px;
    height: 35px;
    font-size: 0.7rem;
  }

  .signin__container:before {
    width: 1500px;
    height: 1500px;
    transform: translateX(-50%);
    left: 30%;
    bottom: 68%;
    right: auto;
    right: initial;
    top: auto;
    top: initial;
    transition: 2s ease-in-out;
  }
}

@media (max-width: 570px) {
  .signin__homeTitle {
    text-align: center;
    margin-right: 20px;
  }
  .signin__form {
    padding: 0 1.5rem;
  }

  .signin__panelImage {
    display: none;
  }
  .signin__panel .signin__panelContent {
    padding: 0.5rem 1rem;
  }
  .signin__container {
    padding: 1.5rem;
  }
  .signin__container:before {
    bottom: 72%;
    left: 50%;
  }
}

