  box-shadow: 2px 2px 12px rgba(0, 0, 0, 0.2), -1px -1px 8px rgba(0, 0, 0, 0.2);
}

.policies__del {
  background: #da1a1a;
  border: none;
  outline: none;
  cursor: pointer;
  border-radius: 5px;
}

.policies__del:hover {
  transition: all 0.2s ease-in;
  transform: scale(1);
  box-shadow: 2px 2px 12px rgba(0, 0, 0, 0.2), -1px -1px 8px rgba(0, 0, 0, 0.2);
}

