.updateProfile {
  display: flex;
  justify-content: center;
  width: 100%;
}

.updateProfile__card {
  max-width: 100%;
  position: relative;
  margin: 30px;
  padding: 30px;
  border-radius: 20px;
  background: #f8f8f8;
  box-shadow: 5px 5px 15px rgba(0, 0, 0, 0.3);
}

.updateProfile__header {
  font-size: 18px;
  margin: 20px 0;
  text-align: center;
}

.updateProfile__heading {
  font-weight: 500;
}

.updateProfile__body {
  width: 100%;
}

.updateProfile__row {
  margin: 30px 0px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.updateProfile__label {
  width: 200px;
  margin-right: 10px;
  font-size: 16px;
}

.updateProfile__star {
  color: red;
}

.updateProfile__input,
.updateProfile__area {
  width: 100%;
  position: relative;
  margin: 15px 0px;
  outline: none;
  font-size: 17px;
  padding: 7px 5px;
  border-radius: 7px;
  border: 1px solid #d5dbd9;
  background: #f8f8f8;
  box-shadow: 1px 1px 10px rgba(2, 0, 22, 0.2);
}

.updateProfile__area {
  resize: none;
  height: 100px;
}

.updateProfile__area:hover,
.updateProfile__input:hover {
  border: 1px solid black;
}

.updateProfile__selectRow {
  position: relative;
  width: 100%;
  height: 37px;
  margin-top: 10px;
}

.updateProfile__select {
  -webkit-appearance: none;
  -moz-appearance: none;
       appearance: none;
  border: 1px solid #d5dbd9;
  width: 100%;
  display: flex;
  justify-content: flex-end;
  height: 100%;
  font-size: 16px;
  font-weight: 500;
  border-radius: 7px;
  padding: 8px 15px;
  background-color: #f8f8f8;
  box-shadow: 1px 1px 10px rgba(2, 0, 22, 0.2);
  outline: none;
  position: relative;
  cursor: pointer;
  border: 0px;
  z-index: 0;
  /* text-transform: uppercase; */
}

.updateProfile__selectRow:before {
  content: "";
  position: absolute;
  top: 15px;
  left: 93%;
  border: 8px solid;
  border-color: #5078ef transparent transparent transparent;
  pointer-events: none;
  z-index: 1;
}

.updateProfile__btn {
  width: 50%;
  height: 45px;
  padding: 10px;
  font-size: 16px;
  font-weight: 550;
  border: 0;
  transition: 0.5s;
  cursor: pointer;
  background-size: 200% auto;
  border-radius: 10px;
  box-shadow: 0 0 20px #eee;
  background-image: linear-gradient(
    to right,
    #71d9fc 0%,
    rgb(68, 148, 253) 51%,
    #65d5fa 100%
  );
  outline: none;
}

.updateProfile__btn:hover {
  background-position: right center;
  text-decoration: none;
}

.updateProfile__rowBtn {
  display: flex;
  justify-content: center;
}

.updateProfile__upload {
  width: 100%;
  position: relative;
  margin: 15px 0px;
  outline: none;
  font-size: 17px;
  border-radius: 7px;
  border: 1px solid #d5dbd9;
  background: #f8f8f8;
  box-shadow: 1px 1px 10px rgba(2, 0, 22, 0.2);
  cursor: pointer;
}

@media (max-width: 940px) {
  .updateProfile__row {
    flex-direction: column;
    align-items: flex-start;
  }
  .updateProfile__label {
    margin-bottom: 5px;
  }
}

