.payslip__card {
  max-width: 100%;
  margin: 30px;
  padding: 30px;
  border-radius: 20px;
  background: #f8f8f8;
  box-shadow: 35px 35px 35px #c7c6c6, -35px -35px 35px #e0dfdf;
}

.payslip__header {
  font-size: 20px;
  margin: 20px 0;
  text-align: center;
}

.payslip__data {
  width: 100%;
  font-size: 21px;
}

.payslip__number {
  font-size: 30px;
  font-weight: 30px;
  display: flex;
  justify-content: flex-end;
}

.top__row {
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  margin: 30px 0px;
  display: flex;
  align-items: center;
  width: 100%;
}

.payslip__details {
  float: right;
  text-align: right;
}

.earnings {
  font-size: 20px;
  width: 50%;
}

.payslip__head {
  font-size: 22px;
}

.payslip__body {
  width: 100%;
}

.payslip__tableHead {
  text-align: left;
  font-size: 25px;
  border-bottom: 1px solid #ddd;
  border: 1px solid #ddd;
  padding: 15px;
  padding-left: 20px;
}

.payslip__tableRow {
  border-bottom: 1px solid #ddd;
  border: 1px solid #ddd;
}

.payslip__tableTd {
  height: 50px;
  vertical-align: middle;
  font-size: 20px;
  border: 1px solid #ddd;
  padding-left: 20px;
}

.payslip__table {
  border: 1px solid #ddd;
  width: 100%;
  border-collapse: collapse;
}

.payslip__input {
  width: 15%;
  height: 20px;
  border: none;
}

.payslip__inputSalary {
  border: none;
  height: 30px;
  font-size: 25px;
}

.payslip__netSalary {
  display: inline;
}

.payslip__button {
  align-items: center;
  margin: 15px;
  height: 50px;
  width: 20%;
  border-radius: 5px;
  box-shadow: 18px 18px 37px #d0d0d0, -18px -18px 37px #ffffff;
  outline: none;
  font-size: 20px;
  cursor: pointer;
}

.payslip__buttonDiv {
  display: flex;
  justify-content: flex-end;
}
